/**
 * css
 */

import isString from 'lodash/isString'

/**
 * css, 
 *
 * @param {*} sel 
 * @returns {Element} html
 */
export default function selectorToElement (sel) {
  let ret = sel

  if (isString(sel)) {
    ret = document.querySelector(sel)
  }

  return ret
}



